*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;   
}

header{
    background-color: #588157;
    padding: 0.8rem;
    color: #DAD7CD;

    display: flex;
    align-items: center;
    justify-content: center; /*alinea horizontalmente*/
}

#img-logo{
    width: 4rem;
    height: 4rem;
    margin-right: 12px;  /*para que no este pegado al titulo*/
}

#img-logo:hover{
    cursor: pointer;  /*se hace un pointer sobre el icono*/
}

body{
    background-color: #dad7cd;
    font-family: sans-serif;
    text-align: center;
}

.content{
    display: flex; /*para que no salga una debajo de la otra*/
    flex-wrap: wrap;
}

.coin{
    background-color: #a3b18a;
    padding: 1.1rem;
    margin: 0.4rem;
    border-radius: 0.3rem;
    display: inline-block;  /*para que no ocupe todo el ancho, solo el necesario*/
}

.imgCoin{
    width: 25px;
    height: 25px;
}

/*----------CSS DE FAQ--------------*/

.FAQS{
    margin: 2rem;
    padding: 0.8rem;
    border-radius: 0.3rem;
    font-family: sans-serif;
    background-color: #588157;
}

.faq{
    background-color: #a3b18a;
    cursor: pointer;
    padding: 0.7rem;
}

.question h3{
    font-size: 1.5rem;
}

.answer{
    max-height: 0;
    overflow: hidden;  /*para ocultar las respuestas*/
}

.faq.active .answer{
    max-height: 300px; 
    border-radius: 4px;
    border-style: solid;
    padding: 2vh;
   
    background-color: rebeccapurple;
}